import { withApiAuth, supabaseServerClient } from "@supabase/auth-helpers-sveltekit";
const get = async ({ locals }) => withApiAuth({
  user: locals.user
}, async () => {
  const { data: profileData, error } = await supabaseServerClient(locals.accessToken).from("profile").select("community_meeting_choices,other_community_meeting").eq("id", locals.user.id);
  console.log("profileMeetings", profileData);
  if (error) {
    console.log("error profileMeetings:", error);
    return {
      status: 400,
      body: { error }
    };
  }
  if (profileData.length === 1) {
    let profileMeetings = profileData[0];
    if (profileMeetings.community_meeting_choices == null) {
      profileMeetings.community_meeting_choices = [];
    }
    return {
      status: 200,
      body: { profileMeetings }
    };
  }
  return {
    status: 400,
    body: {}
  };
});
const post = async ({ locals, request }) => withApiAuth({
  user: locals.user
}, async () => {
  const body = await request.formData();
  console.log("first_name", body.get("first_name"));
  const { data: profileMeetings, error } = await supabaseServerClient(locals.accessToken).from("profile").update({
    community_meeting_choices: body.get("community_meeting_choices"),
    other_community_meeting: body.get("other_community_meeting")
  }).eq("id", locals.user.id);
  if (error) {
    console.log("update error profileMeetings:", error);
    return {
      status: 400,
      body: { error }
    };
  }
  return {
    body: { profileMeetings }
  };
});
export { get, post };
